import withNavigate from "../WithNavigate";
import React from "react";

class FinesseProfileSection extends React.Component {
  handleClick = () => {
    const { navigate } = this.props;
    navigate("/login");
  };

  render() {
    let outerStyles = {
      position: "absolute",
      padding: "2vh 2vw",
    };

    let styles = {
      left: "0px",
      top: "0px",
      width: "auto",
      height: "auto",
      // backgroundColor: "red",
      // padding: "2vh 2vw",
      textAlign: "center",
    };

    let imageStyles = {
      width: "auto",
      height: "72px",
      overflow: "hidden",
      // backgroundColor: "green",
      cursor: "pointer",
      textAlign: "center",
    };

    let textStyles = {
      marginTop: "10px",
      visibility: "hidden",
    };

    return (
      <div style={outerStyles}>
        <div style={styles}>
          <div style={imageStyles}>
            <img
              src={require("./../res/profile.png")}
              alt="Profile"
              style={{ height: "100%" }}
              onClick={this.handleClick}
            />
          </div>
          <div style={textStyles}>@finesseMain</div>
        </div>
      </div>
    );
  }
}

export default withNavigate(FinesseProfileSection);
