import React from 'react';

class FinesseInput extends React.Component {
  render() {
    let styles = {
        width: "100%",
        height: "40px",
        borderRadius: "5px",
        border: "0px solid #ccc",
        padding: "0 10px",
        boxSizing: "border-box",
        marginBottom: "10px",
        fontSize: "16px",
        outline: "none",
        fontWeight: "bold",
        fontFamily: "Inter"
    }
    return <input type={this.props.type} placeholder={this.props.placeholder} style={styles}/>;
  }
}

export default FinesseInput;
