import React from 'react';

class FinesseButton extends React.Component {

  handleClick = (e) => {}

  render() {
    let styles = {
        width: "100%",
        height: "40px",
        borderRadius: "5px",
        border: "5px solid #fff",
        padding: "0 10px",
        boxSizing: "border-box",
        marginBottom: "10px",
        fontSize: "16px",
        outline: "none",
        backgroundColor: "white",
        cursor: "pointer",
        color: 'rgba(0,0,0,1)',
        fontWeight: 'bold',
        boxShadow: "3px 3px 0px rgba(0,0,0,0.1)",
    }
    return <button style={styles} onClick={(e) => this.handleClick(e)}>{this.props.value}</button>;
  }
}

export default FinesseButton;
