import { useState, useEffect, useCallback, useRef } from "react";
import FinesseContentCard from "./FinesseContentCard";
import CanvasJSReact from "./../canvasjs.react";
import { motion } from "framer-motion";
import axios from "axios";

var CanvasJS = CanvasJSReact.CanvasJS;
var CanvasJSChart = CanvasJSReact.CanvasJSChart;

function FinesseContentSection() {
  const scroller = useRef(null);
  let hasEventListener = false;

  useEffect(() => {
    if (scroller && scroller.current && !hasEventListener) {
      scroller.current.addEventListener("wheel", scrollListener, {
        passive: false,
      });
      hasEventListener = true;
    }
  }, []);

  var myList = new Array(
    {
      source: "youtube",
      url: "https://www.youtube.com/embed/QH2-TGUlwu4?autoplay=1",
    },
    {
      source: "youtube",
      url: "https://www.youtube.com/embed/AZals4U6Z_I",
    },
    {
      source: "youtube",
      url: "https://www.youtube.com/embed/QH2-TGUlwu4",
    }
  );
  const [contentList, setContentList] = useState([...myList]);
  const [focusedIndex, setFocusedIndex] = useState(0);

  let offsetMap = {
    0: -500,
    1: 100,
    2: 700,
  };

  const [pos, setPos] = useState(0);
  useEffect(() => {
    console.log("position:", pos);
  }, [pos]);

  const scrollUp = () => {
    setPos((oldPos) => oldPos + 600);
    console.log("scroll up", pos);
  };

  const scrollDown = () => {
    setPos((oldPos) => oldPos - 600);
    console.log("scroll down", pos);
  };

  const handler = async (req, res) => {
    const options = {
      method: "POST",
      url: "http://math.church/query",
      headers: {
        "content-type": "application/json",
      },
      data: {
        query: `{
                  content(num: 4) {
                      source,
                      url
                  }
              }`,
      },
    };

    axios
      .request(options)
      .then(function (response) {
        console.log(response.data.data.content); // Response
        // join with contentList
        setContentList([...contentList, ...response.data.data.content])
      })
      .catch(function (error) {
        console.error(error);
      });
  };

  const loadNextN = () => {
    // fetch('https://api.npms.io/v2/search?q=react')
    //     .then(response => response.json())
    //     .then(data => this.setState({ totalReactPackages: data.total }));
    handler();

    // add new item to contentList
    // contentList.push({
    //   source: "youtube",
    //   url: "https://www.youtube.com/embed/AZals4U6Z_I",
    // });
    // console.log("load next n", contentList);
  };

  useEffect(() => {
    console.log("focused index:", focusedIndex);
    if (focusedIndex === contentList.length - 1) {
      loadNextN();
    }
  }, [focusedIndex]);

  let contentCards = contentList.map((contentObject, i) => {
    let top = 100 + 600 * i + pos;
    if ((top > 50) &&( top < 200) && focusedIndex !== i) {
       setFocusedIndex(i);
    }
    return (
      <FinesseContentCard
        {...contentObject}
        y={offsetMap[i]}
        key={i}
        index={i}
        position={top}
      />
    );
  });

  let [points, setPoints] = useState([]);
  let [focusedElement, setFocusedElement] = useState(contentCards[0]);
  let lastPoints = [];

  function scrollListener(e) {
    points.push({
      x: new Date(),
      y: e.deltaY,
    });
    let last = points[0].x;
    let newPoints = points.filter((point, i) => {
      if (i === 0) return true;
      if (i < 4) return false;
      if (
        point.y > 0 &&
        point.x - last > 200 &&
        points[i].y > points[i - 1].y &&
        points[i - 1].y > points[i - 2].y
      ) {
        last = point.x;
        return true;
      } else if (
        point.y < 0 &&
        point.x - last > 200 &&
        points[i].y < points[i - 1].y &&
        points[i - 1].y < points[i - 2].y
      ) {
        last = point.x;
        return true;
      }
    });

    if (newPoints.length !== lastPoints.length) {
      if (newPoints[newPoints.length - 1].y > 0) scrollDown();
      else scrollUp();
      lastPoints = newPoints;
    }

    options.data[0].dataPoints = newPoints;
    setOptions({ ...options });
    e.preventDefault();
    e.stopPropagation();
    return false;
  }

  const [options, setOptions] = useState({
    title: {
      text: "scroll",
    },
    // willReadFrequently: true,
    data: [
      {
        type: "line",
        dataPoints: points,
      },
    ],
  });

  return (
    <div
      style={{
        backgroundColor: "yellow",
        height: "100vh",
        width: "100vw",
        position: "absolute",
        top: "0px",
        overflow: "hidden",
      }}
      ref={scroller}
      tabIndex="0"
      // onWheel={(e) => {
      //   console.log(4);
      //   e.preventDefault();
      //   e.stopPropagation();
      //   return false
      // }}
      // onScroll={(e) => {
      //   console.log(5);
      //   e.preventDefault();
      //   e.stopPropagation();
      //   return false
      // }}
    >
      {/* <CanvasJSChart options={options} /> */}
      <div
        style={{
          backgroundColor: "",
          width: "50vw",
          marginLeft: "28vw",
        }}
        onWheel={(e) => {
          console.log("wheel", e);
          e.preventDefault();
          e.stopPropagation();
          return false;
        }}
        onScroll={(e) => {
          console.log("scroll", e);
          e.preventDefault();
          e.stopPropagation();
          return false;
        }}
      >
        {contentCards}
      </div>
    </div>
  );
}

export default FinesseContentSection;
