import React from "react";
import FinesseInput from "./FinesseInput";
import FinesseButton from "./FinesseButton";
import withNavigate from "../WithNavigate";
import { Link } from "react-router-dom";

class LoginPage extends React.Component {
  handleClick = () => {
    const { navigate } = this.props;
    navigate("/");
  };

  render() {
    let styles = {
      width: "400px",
      // height: "400px",
      margin: "auto",
      // marginTop: "100px",
      // backgroundColor: "white",
      padding: "20px",
      borderRadius: "5px",
      // boxShadow: "0 0 10px #ccc",
    };

    let imageStyles = {
      // width: "100px",
      height: "50px",
      margin: "auto",
      display: "block",
      marginBottom: "20px",
    };

    let loginPageStyles = {
      backgroundColor: "yellow",
      height: "100vh",
      width: "100vw",
      top: "0px",
      paddingTop: "25vh",
      fontFamily: "Inter"
    };

    return (
      <div style={loginPageStyles}>
        <div style={styles}>
          <img
            src={require("./../res/logo.png")}
            alt="Finesse Logo"
            style={imageStyles}
          />
          <FinesseInput placeholder="Email" />
          <FinesseInput placeholder="Password" type="password" />
          <Link to={'/'}><FinesseButton value="Login"/></Link>
        </div>
      </div>
    );
  }
}

export default withNavigate(LoginPage);
